import React from "react";

const ScatterQuotesApexChartOptionsLazy = React.lazy(() => import("./ScatterQuotesApexChartOptionsLazy.js"))

export default (props) => {
    const isSSR = typeof window === "undefined"
    return <>
      {!isSSR && (
        <React.Suspense fallback={<div />}><ScatterQuotesApexChartOptionsLazy {...props} /></React.Suspense>
      )}
    </>
}
