import React from "react";

const DatalineQuotesApexChartOptionsLazy = React.lazy(() => import("./DatalineQuotesApexChartOptionsLazy.js"))

export default (props) => {
    const isSSR = typeof window === "undefined"
    return <>
      {!isSSR && (
        <React.Suspense fallback={<div />}><DatalineQuotesApexChartOptionsLazy {...props} /></React.Suspense>
      )}
    </>
}
  
  
