import React from "react";
import ReactMarkdown from "react-markdown";
import firebase from "gatsby-plugin-firebase";
import { useFirebaseNode } from "../hooks/useFirebaseNode";

import { useSelector } from "react-redux";

import Layout from "../components/layout"
import {
    selectApplication,
} from "../features/application/applicationSlice";

import { alphabet } from "../features/spreadsheet/AlphabetRow";
import { createProtoArray } from "../features/spreadsheet/spreadsheetSlice";

import ScatterQuotesApexChartOptions from "../mediatemplates/ScatterQuotesApexChartOptions";
import DatalineQuotesApexChartOptions from "../mediatemplates/DatalineQuotesApexChartOptions";


import Alert from 'react-bootstrap/Alert'
import Table from 'react-bootstrap/Table'



export default function MyWokBook() {
    const [data, setData] = React.useState(null);
    const email = useSelector(selectApplication).email;
    let userEmail = email.replace(/[^a-zA-Z0-9]/g, "_");
    const avatarUrl = useSelector(selectApplication).avatarUrl;
    const user = useSelector(selectApplication).user;

    React.useEffect(() => {
        firebase
            .database()
            .ref('usersCraft/' + userEmail + '/posts/')
            .once("value")
            .then(snapshot => {
                let userPosts = [];
                snapshot.forEach(function (childSnapshot) {
                    // key will be "ada" the first time and "alan" the second time
                    //     var key = childSnapshot.key;
                    // childData will be the actual contents of the child
                    let childData = childSnapshot.val();
                    userPosts.push(childData);
                });
                setData(userPosts);
                //           console.log(userPosts);
            })
            .catch(err => console.log(err));

    }, [])

    const uniqueThemes = !!data ? [...new Set(data.map(item => item.theme))] : []; // [ 'A', 'B']

    return <Layout>
        <div className="media border p-3 mb-5">
            <img src={avatarUrl} alt=""
                className="m-3"
                style={{
                    verticalAlign: 'middle',
                    width: '60px',
                    height: '60px',
                    borderRadius: '50%',
                    filter: 'grayscale(100%)',
                    objectFit: 'cover'
                }} />
            <div className="media-body">
                <h4>{user} <small><i>{new Intl.DateTimeFormat("ru", {
                    weekday: "short",
                    year: "numeric",
                    month: "short",
                    day: "numeric",
                    hour: "numeric",
                    minute: "numeric",
                }).format(new Date())}</i></small></h4>
                <p>Рабочая тетрадь</p>
            </div>
        </div>
        <div>{!!uniqueThemes && uniqueThemes.map(theme =>
            <div key={theme}>
                <Alert variant='primary'>
                    {theme}
                </Alert>
                {data.filter(quiz => quiz.theme === theme).map(quiz => {
                    let xlArray = quiz.type === 'spreadsheet' && !!quiz?.content ? createProtoArray(quiz.content, 0, 0) : null;
                    //      user: user, avatarUrl: !!avatarUrl ? avatarUrl : null,


                    return <div key={quiz.id} >
                        <Alert variant='secondary' className="mt-3">
                            {!!quiz.avatarUrl ? <img src={quiz.avatarUrl} alt=""
                                className="m-2"
                                style={{
                                    verticalAlign: 'middle',
                                    width: '50px',
                                    height: '50px',
                                    borderRadius: '50%',
                                    filter: 'grayscale(100%)',
                                    objectFit: 'cover'
                                }} /> : null}
                            {quiz.title}
                        </Alert>
                        <ReactMarkdown source={quiz.quizString + `<br><span>Ответ: ${quiz.answer}</span><span class="ml-5">Дата/время: ${quiz.date}</span><br>${!!quiz.user ? 'Решено: ' + quiz.user : ""}`} escapeHtml={false} />
                        {quiz.type === 'spreadsheet' && !!xlArray ?
                            <Table striped bordered hover>
                                <thead>
                                    <tr>
                                        <th>{" "}</th>
                                        {xlArray[0].map((column, columnIndex) => <th key={columnIndex} className="text-center">{alphabet[columnIndex]}</th>)}
                                    </tr>
                                </thead>
                                <tbody>
                                    {xlArray.map((row, rowIndex) =>
                                        <tr key={rowIndex} >
                                            <td> <small className="ml-1">{rowIndex + 1}</small></td>
                                            {row.map((column, columnIndex) => <td key={rowIndex + columnIndex}><small>{row[columnIndex]}</small></td>)}
                                        </tr>
                                    )}
                                </tbody>
                            </Table>
                            : null}

                        {quiz.type === "media" ?
                            <ShowMedia datafirenode={'/usersCraft/' + userEmail + '/data/' + quiz.content} />
                            : null}
                    </div>
                })}
            </div>
        )}
        </div>
    </Layout>
}


function ShowMedia(props) {
    const { data: mediaItems, loading, error } = useFirebaseNode(props.datafirenode);
  //  console.log(props.content, mediaItems);

    if (!!loading || !mediaItems) { return <div>...</div> }

    return <div>
        {Object.keys(mediaItems).map((mediaItem, index) => {
            if (mediaItem === 'scatterquotesapexchartoptions') {
                let series = Object.keys(mediaItems[mediaItem].series).map(scatter => { return [mediaItems[mediaItem]['series'][scatter][0], mediaItems[mediaItem]['series'][scatter][1]] }
                );
         //       console.log(series)
                return <ScatterQuotesApexChartOptions key={index} series={series} />
            }
            if (mediaItem === 'datalinequotesapexchartoptions') {
                let series = Object.keys(mediaItems[mediaItem].series).map(line => {
                    return {
                        name: mediaItems[mediaItem]['series'][line]['name'],
                        data: Object.keys(mediaItems[mediaItem]['series'][line]['data']).map(line_point => {
                            return [mediaItems[mediaItem]['series'][line]['data'][line_point][0], mediaItems[mediaItem]['series'][line]['data'][line_point][1]]
                        })
                    }
                });
     //           console.log(series);
                return <DatalineQuotesApexChartOptions key={index} series={series} />
            }
            return null
        })}
    </div>
}
